  -moz-transition: height 0.5s linear;
  transition: height 0.5s linear;
}

.smalltopbar a {
  color: #fff;
  font-size: 1.3em;
  line-height: 1.25em;
  text-decoration: none;
  opacity: 1;
  font-weight: bold;
}

